
header {
  min-width: 1200px;
  height: 60px;
  font-size: 14px;
  background: #2a2a2a;
}
header .logo {
    float: left;
    margin-top: 14px;
    margin-left: 20px;
    width: 120px;
}
header .analysisLogo {
    float: left;
    margin-top: 17px;
    margin-left: 20px;
}
header nav {
    float: left;
    margin-top: 12px;
    margin-left: 100px;
}
header .nav-item {
    display: inline-block;
    width: 90px;
    height: 36px;
    line-height: 36px;
    color: #999999;
    text-align: center;
    border-radius: 2px;
    cursor: pointer;
    -webkit-transition: all .5s linear;
    transition: all .5s linear;
}
header .nav-item:hover {
      color: #ffffff;
      background-color: #2a2a2a;
}
header .nav-item.active-module {
      color: #ffffff;
      background: #3c96ff;
}
header .header-right {
    float: right;
    padding-right: 20px;
}
header .header-right .biz-list {
      display: inline-block;
      vertical-align: top;
}
header .header-right .biz-list .bk-selector-input {
        height: 60px;
        line-height: 60px;
        border: none;
        background: #333;
        color: #fff;
}
header .header-right .biz-list .bk-selector-icon {
        top: 23px;
}
header .header-right .user-name {
      display: inline-block;
      margin-left: 20px;
      height: 60px;
      line-height: 60px;
      font-size: 14px;
      color: #666;
}
