.common-section-title {
  position: relative;
  margin: 0;
  padding: 0 0 0 16px;
  line-height: 1;
  font-size: 16px;
  font-weight: normal;
  color: #666666;
}
.common-section-title:before {
    content: '';
    display: inline-block;
    position: absolute;
    top: -2px;
    left: 0;
    width: 4px;
    height: 20px;
    background: #3c96ff;
}
.common-form-item {
  margin-bottom: 20px;
}
.common-form-item::after {
    display: block;
    content: "";
    clear: both;
}
.common-form-item:last-child {
    margin-bottom: 0;
}
.common-form-item > label {
    position: relative;
    float: left;
    margin-top: 6px;
    width: 100px;
    font-size: 14px;
    font-weight: bold;
    color: #666666;
    text-align: right;
}
.common-form-item > label.required:after {
      content: '*';
      position: absolute;
      top: 0px;
      right: -10px;
      color: #ff2602;
      font-family: "SimSun";
}
.common-form-item .common-form-content {
    margin-left: 120px;
    min-height: 36px;
}
.common-dialog .bk-dialog-header {
  padding: 20px 30px;
  border-bottom: 1px solid #ebebeb;
}
.common-dialog .bk-dialog-header .bk-dialog-title {
    text-align: left;
}
.common-dialog .bk-dialog-tool .bk-dialog-close {
  margin-top: 21px;
  margin-right: 20px;
}
.common-error-tip {
  display: inline-block;
  font-size: 14px;
  color: #ff5757;
}
.bk-dialog-wrapper .bk-dialog-footer .bk-dialog-outer button {
  width: 90px;
}
body {
  color: #333333;
  font-family: 'Microsoft YaHei','PingFang SC','Hiragino Sans GB','SimSun','sans-serif';
}
.clearfix::after {
  display: block;
  content: "";
  clear: both;
}
html, body {
  height: 100%;
}
#app {
  height: 100%;
}
